import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';

function meterToFeet(value) {
    return 3.28084 * value;
}

@observer
class Elevation extends Component {
    render() {
		const { value = 0, smallUnit } = this.props;
		const { userStore } = this.props.stores;
		const { user } = userStore;
		const { settings = {} } = user;

        if (settings.imperial) {
            return (<>
                <nobr>
                    {util.format(meterToFeet(value), 0)} {smallUnit ? <><small><small><small class='ml-0 text-muted'>ft</small></small></small></> : <>ft</>}
                </nobr>
            </>);
        }

		return (<>
            <nobr>
                {util.format(value, 0)} {smallUnit ? <><small><small><small class='ml-0 text-muted'>m</small></small></small></> : <>m</>}
            </nobr>
        </>);
    }
}

export default Elevation;
